/**
* animation
*/
@-moz-keyframes music {
  0% {
    transform: scale(1);
  }
  25% {
    transform: scale(1.2);
  }
  50% {
    transform: scale(1);
  }
  75% {
    transform: scale(0.8);
  }
  100% {
    transform: scale(1);
  }
}
@-o-keyframes music {
  0% {
    transform: scale(1);
  }
  25% {
    transform: scale(1.2);
  }
  50% {
    transform: scale(1);
  }
  75% {
    transform: scale(0.8);
  }
  100% {
    transform: scale(1);
  }
}
@-webkit-keyframes music {
  0% {
    transform: scale(1);
  }
  25% {
    transform: scale(1.2);
  }
  50% {
    transform: scale(1);
  }
  75% {
    transform: scale(0.8);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes music {
  0% {
    transform: scale(1);
  }
  25% {
    transform: scale(1.2);
  }
  50% {
    transform: scale(1);
  }
  75% {
    transform: scale(0.8);
  }
  100% {
    transform: scale(1);
  }
}
.root {
  width: 100%;
  height: 100%;
  position: relative;
}
.root .music {
  position: absolute;
  top: 12px;
  right: 12px;
  z-index: 2;
}
.root .music.play {
  animation: music 3s 0s linear infinite;
}
.root .music i {
  font-size: 30px;
  color: red;
}
.root .space-carousel {
  touch-action: none;
  height: 100%!important;
}
.root .space-carousel .slider-frame {
  height: 100%!important;
}
.root .space-carousel .slider-frame .slider-list {
  height: 100%!important;
}
.root .space-carousel .slider-frame .slider-list .slider-slide {
  height: 100%!important;
}
